import { Toast } from "vant";
import { transferAccount } from "jcc_exchange";
import { JcWalletTool } from "jcc_wallet";
import { mapGetters } from "lib";
// import { JcExchange } from "jcc_rpc";
// import { getErrorMsg } from "js/utils";

export default {
  data() {
    return {
      loading: false,
      showConfirmDialog: false,
      form: {
        payeeName: "",
        walletAddress: "",
        AmountCollected: "",
        remark: "",
        currency: { name: "", clicked: false },
        tradePsw: ""
      },
    }
  },
  computed: {
    checkForm() {
      return (
        this.form.payeeName !== "" &&
        this.form.walletAddress !== "" &&
        this.form.AmountCollected !== "" &&
        this.form.currency.name !== ""
      );
    },
    ...mapGetters(["swtAddress", "jcWallet"])
  },
  methods: {
    getBaseName(base) {
      if (base === "CNT") {
        return "CNY";
      } else if (base === "SWTC") {
        return "SWT";
      } else {
        return base;
      }
    },
    confirmTransfer() {
      if (!this.checkForm || this.loading) {
        this.showConfirmDialog = false;
        this.form.tradePsw = "";
        return;
      }
      this.showConfirmDialog = false;
      this.loading = true;
      Toast.loading({
        duration: 0,
        forbidClick: true,
        message: this.$t("message.transferPayment.payments.transfering")
      });
      this.orderWithtradePsw();
    },
    orderWithtradePsw() {
      let jcTool = new JcWalletTool(this.$store.getters.jcWallet);
      let proEnv = process.env.NODE_ENV === "production";
      let hosts = proEnv ? this.hosts : [window.location.hostname];
      let port = proEnv ? this.port : "8080";
      jcTool
        .validatePassword(this.form.tradePsw, "swt")
        .then(secret => {
          var data = {};
          data.issuer = process.env.currencies.UST.issuer;
          data.currency = this.getBaseName(this.form.currency.name);
          data.address = this.swtAddress;
          data.to = this.form.walletAddress;
          data.amount = this.form.AmountCollected;
          data.memo = this.form.remark;
          data.secret = secret;
          data.hosts = hosts;
          data.port = port;
          data.https = this.https;
          setTimeout(() => {
            // 转账接口
            this.confirmPay(data);
          }, 1000);
        })
        .catch(error => {
          Toast.fail(error.message);
        })
        .finally(() => {});
    },
    confirmPay(data) {
      console.log("开始转账");
      console.log(data);
      transferAccount(data)
        .then(() => {
          Toast.success("转账成功");
        })
        .catch(error => {
          console.log(error.message);
          Toast.fail(error.message);
        });
    }
  }
}


